@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{font-family:"Open Sans",sans-serif}.repo-card-div{color:#586069;background-color:#fff;box-shadow:#75e0a2 0px 10px 50px -5px;padding:2rem;border-radius:5px;cursor:pointer;transition:all .3s ease}.repo-card-div:hover{box-shadow:rgba(0,0,0,.2) 0px 20px 30px -10px}.repo-stats{display:flex;justify-content:space-between;font-size:13px;color:#6a737d}.repo-left-stat{flex-grow:1;display:flex}.language-color{width:10px;height:10px;background-color:#27ae60;margin-right:.25rem;border-radius:100%}.repo-left-stat span{display:flex;align-items:center;margin-right:.75rem}.repo-name-div{display:flex;align-items:center}.repo-svg{margin-right:.5rem;min-width:16px}.repo-name{white-space:nowrap;text-overflow:ellipsis;color:#24292e;margin-bottom:.75rem;font-size:25px;font-weight:700;letter-spacing:-0.5px;overflow:hidden;margin:0px;height:30px}.repo-star-svg{margin-right:.3rem}.repo-description{overflow:hidden;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical}.dark-card-mode{background-color:#101010;color:#fff}.dark-card-mode p{color:#fff}.dark-card-mode .repo-svg{fill:#fff}
